.slideshow-container {
  text-align: center;
  transform: translateY(-32px);
  @media screen and (min-width: 768px) {
    transform: translateY(-112px);
  }
}

.gallery-img {
  border-radius: 8px;
  @media screen and (min-width: 320px) {
    width: 280px;
  }

  @media screen and (min-width: 768px) {
    border-radius: 18px;
    width: 704px;
  }
  @media screen and (min-width: 1200px) {
    width: 1013px;
    border-radius: 24px;
  }
}
