.products {
  padding-top: 60px;
  padding-bottom: 60px;
  background-image: url(../images/products/bg_sm.jpg);
  background-repeat: repeat;
  background-size: contain;
  background-position: center;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/products/bg_sm@2x.jpg);
  }

  @media screen and (min-width: 768px) {
    padding-bottom: 82px;
    background-image: url(../images/products/bg_md.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/products/bg_md@2x.jpg);
    }
  }

  @media screen and (min-width: 1200px) {
    padding-bottom: 66px;
    background-image: url(../images/products/bg_lg.jpg);

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/products/bg_lg@2x.jpg);
    }
  }
}

.products-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 164px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    gap: 16px;
  }

  @media screen and (min-width: 1200px) {
    gap: 32px;
  }
}

.products__title {
  margin-bottom: 148px;

  // @media screen and(min-width: 768px) {
  //     margin-bottom: 200px;
  // }

  // @media screen and(min-width: 1200px) {
  //     margin-bottom: 303px;
  // }

  font-family: 'Seymour One', sans-serif;
  //  margin-bottom: 12px;
  font-weight: 400;
  font-size: 30px;
  line-height: 1.27;
  text-align: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: $textColor;

  @media screen and (min-width: 768px) {
    margin-bottom: 200px;
    font-size: 34px;
    line-height: 1.26;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 303px;
    font-size: 36px;
    line-height: 1.25;
  }
}

.products__pretitle {
  margin-bottom: 12px;

  font-weight: 800;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  text-transform: uppercase;
  color: $textColor;

  @media screen and (min-width: 768px) {
    font-size: 22px;
    line-height: 1.23;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.23;
  }
}

.product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 280px;
  height: 420px;
  border-radius: 24px;

  padding-top: 123px;
  padding-bottom: 16px;
  padding-left: 40px;
  padding-right: 40px;

  @media screen and (min-width: 768px) {
    width: 224px;
    height: 428px;
    padding-top: 108px;
    padding-left: 26px;
    padding-right: 26px;
    padding-bottom: 12px;
  }

  @media screen and (min-width: 1200px) {
    width: 370px;
    height: 535px;
    padding-top: 165px;
    padding-bottom: 60px;
    padding-left: 64px;
    padding-right: 64px;
  }
}

.ice-cream {
  background-color: $productsFirstBackgroundColor;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 242px;
    min-height: 241px;
    background-image: url(../images/products/img-1_sm.png);
    // background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/products/img-1_sm@2x.png');
    }
  }

  @media screen and (min-width: 768px) {
    &::before {
      min-width: 239px;
      min-height: 237px;
      background-image: url(../images/products/img-1_md.png);
      // background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('../images/products/img-1_md@2x.png');
      }
    }
  }

  @media screen and (min-width: 1200px) {
    &::before {
      min-width: 363px;
      min-height: 361px;
      background-image: url(../images/products/img-1_lg.png);
      // background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('../images/products/img-1_lg@2x.png');
      }
    }
  }
}

