.product-card__title {
  margin-bottom: 40px;

  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;

  color: $textColor;

  @media screen and (min-width: 768px) {
    font-size: 22px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    margin-bottom: 65px;
  }
}

.product-card__text {
  line-height: 1.76;
  // width: 200px;
  text-align: center;
  margin: 0 auto;
  // letter-spacing: 0.04em;

  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

.product-card__button {
  // display: inline-flex;
  width: 60px;
  // font-family: inherit;
  display: flex;
  justify-content: center;
  // text-align: center;
  // align-items: center;
  margin-top: auto;
  padding: 10px 0;
  background-color: transparent;
  outline: transparent;
  border: none;
  cursor: pointer;

  transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:focus .product-card__icon,
  &:hover .product-card__icon {
    scale: 1.4;
    fill: $bottonActiveBackgroundColor;
  }
}

.product-card__icon {
  transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1),
    fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  // rotate 250ms cubic-bezier(0.4, 0, 0.2, 1) ;

  cursor: pointer;

  &:hover,
  &:focus {
    fill: $bottonActiveBackgroundColor;
    scale: 1.4;
    // rotate: 360deg;
  }
}
